import React, { Component } from 'react';
import classnames from 'classnames';
import { Tabs, Icon } from 'antd';
import {
  Link,
} from 'react-router-dom';
import RankContainer from './container/index';

import './index.scss';

const { TabPane } = Tabs;

class RankList extends Component {
  constructor(props) {
    super(props);
    const panes = [
      { title: '日榜', index_title: '日', key: '1' },
      { title: '周榜', index_title: '周', key: '2' },
      { title: '总榜', index_title: '总', key: '3' },
    ];
    this.state = {
      key: panes[0].key,
      panes,
    };
  }

  toRanking = () => {
    console.log('a');
  }

  tableChange = (key) => {
    console.log(key);
    this.setState({
      key,
    });
  }

  render() {
    const {
      sort, title, noticeStyle, noticeText, path, data, handleData, type,
    } = this.props;
    const sorts = sort || 'anchor';
    const tit = title || '';
    const noticeStyles = noticeStyle || {
      background: 'rgba(241, 158, 194, 1)',
    };
    let html = null;
    let text = null;
    const operations = <Link to="/ranking"><Icon onClick={this.toRanking} type="right" /></Link>;
    if (noticeText) {
      text = (
        <div className={classnames('notice')} style={noticeStyles}>
          {noticeText}
        </div>
      );
    }

    if (path === 'ranking') {
      html = (
        <section className={classnames('section', sorts, `${path}_rank`)}>
          <h3 className={classnames('title')}>
            <span /> &nbsp;&nbsp;{tit}
          </h3>

          <Tabs activeKey={this.state.key} onChange={activeKey => this.tableChange(activeKey)}>
            {this.state.panes.map(pane => (
              <TabPane tab={pane.title} key={pane.key}>
                <div className={classnames('content')}>
                  {text}
                  <RankContainer
                    path={path}
                    data={data}
                    handleData={handleData}
                    activeKey={this.state.key}
                    type={type}
                  />
                </div>
              </TabPane>
            ))}
          </Tabs>
        </section>
      );
    } else {
      html = (
        <section className={classnames('section', sorts, `${path}_rank`)}>
          <h3 className={classnames('title')}>
            <span /> &nbsp;&nbsp;{tit}
          </h3>

          <Tabs
            activeKey={this.state.key}
            tabBarExtraContent={operations}
            onChange={activeKey => this.tableChange(activeKey)}
          >
            {this.state.panes.map(pane => (
              <TabPane tabBarGutter={0} tab={pane.index_title} key={pane.key}>
                <div className={classnames('content')}>
                  {text}
                  <RankContainer
                    path={path}
                    data={data}
                    handleData={handleData}
                    activeKey={this.state.key}
                    type={type}
                  />
                </div>
              </TabPane>
            ))}
          </Tabs>
          <div className='border'></div>
        </section>
      );
    }

    return (
      <div className='ranking_list'>
        { html }
      </div>
    );
  }
}

export default RankList;
