import React, { Component } from 'react';
import classnames from 'classnames';
import Lottie from 'react-lottie';
import animationData from '../../../asset/img/lottie/my_live.json';

import './index.scss';

// 排行榜 动效组件
class LottieS extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isStopped: false,
      isPaused: false,
    };
  }

  render() {
    const defaultOptions = {
      loop: true,
      autoplay: true,
      animationData,
    };
    const { width, height, styleName } = this.props;
    const status = width && height;
    const sty = styleName || 'avatar-none';
    return (
      <div
        className={classnames(
          {
            avatar: status,
          },
          sty,
        )}
      >
        {width && height ? (
          <Lottie
            options={defaultOptions}
            height={width}
            width={height}
            isStopped={this.state.isStopped}
            isPaused={this.state.isPaused}
          />
        ) : (
          ''
        )}
      </div>
    );
  }
}

export default LottieS;
